        style="margin: 0 20px;"
        v-model="options.quality"
        :max="1"
      >
      </el-slider>
    </el-card>

    <!--  -->
    <el-card
      class="box-card"
      shadow="never"
      body-style="padding: 0px;"
      style="margin-top: 20px;"
    >
      <div slot="header">
        <span>
          
          <el-popover placement="top" trigger="click">
            <div class="sunken">
              <span></span>
            </div>
            <i class="fa fa-question-circle-o" slot="reference"></i>
          </el-popover>
        </span>
        <el-button plain @click="onResetOptionsClick">
          <i class="fas fa-redo-alt"></i>
          &nbsp;
        </el-button>
      </div>
      <el-form
        style="height: calc(100vh - 300px); padding: 20px; overflow-y: auto;"
        label-width="97px"
        label-position="left"
      >
        <el-form-item label=""
          ><el-switch
            active-color="#13ce66"
            inactive-color="#ff4949"
            v-model="options.strict"
          >
          </el-switch>
          <el-popover placement="top" trigger="click">
            <div class="sunken">
              <span
                ></span
              >
              <ul style="margin: 12px 0 0;">
                <li></li>
                <li></li>
                <li></li>
                <li></li>
                <li>MIMEMIME</li>
              </ul>
            </div>
            <i class="fa fa-question-circle-o" slot="reference"> </i>
          </el-popover>
        </el-form-item>
        <el-form-item label=""
          ><el-switch
            active-color="#13ce66"
            inactive-color="#ff4949"
            v-model="options.checkOrientation"
          >
          </el-switch>
          <el-popover placement="top" trigger="click">
            <div class="sunken">
              <span
                >ExifJPEG</span
              >
              <ul style="margin: 12px 0 0;">
                <li>
                  JPEG
                </li>
                <li>
                  10
                  MB
                </li>
                <li>
                  ExifExif
                </li>
              </ul>
            </div>
            <i class="fa fa-question-circle-o" slot="reference">
            </i> </el-popover
        ></el-form-item>
        <el-form-item label="">
          <el-input-number
            :min="1"
            v-model="options.maxWidth"
            placeholder=""
            @change="onMaxWidthChange"
          ></el-input-number>
          
          <el-popover placement="top" trigger="click">
            <div class="sunken">
              <span>0</span>
              <div style="margin-top: 15px;">
                <a
                  href="https://stackoverflow.com/questions/6081483/maximum-size-of-a-canvas-element"
                  target="_blank"
                  >canvas</a
                >4096
              </div>
            </div>
            <i class="fa fa-question-circle-o" slot="reference"> </i>
          </el-popover>
        </el-form-item>
        <el-form-item label="">
          <el-input-number
            :min="1"
            v-model="options.maxHeight"
            placeholder=""
            @change="onMaxHeightChange"
          ></el-input-number>
          
          <el-popover placement="top" trigger="click">
            <div class="sunken">
              <span>0</span>
              <div style="margin-top: 15px;">
                <a
                  href="https://stackoverflow.com/questions/6081483/maximum-size-of-a-canvas-element"
                  target="_blank"
                  >canvas</a
                >4096
              </div>
            </div>
            <i class="fa fa-question-circle-o" slot="reference"> </i>
          </el-popover>
        </el-form-item>
        <el-form-item label="">
          <el-input-number
            :min="1"
            :max="options.maxWidth"
            v-model="options.minWidth"
            placeholder=""
          ></el-input-number>
          
          <el-popover placement="top" trigger="click">
            <div class="sunken">
              <span>0</span>
            </div>
            <i class="fa fa-question-circle-o" slot="reference"> </i>
          </el-popover>
        </el-form-item>
        <el-form-item label="">
          <el-input-number
            :min="1"
            :max="options.minWidth"
            v-model="options.minHeight"
            placeholder=""
          ></el-input-number>
          
          <el-popover placement="top" trigger="click">
            <div class="sunken">
              <span>0</span>
            </div>
            <i class="fa fa-question-circle-o" slot="reference"> </i>
          </el-popover>
        </el-form-item>
        <el-form-item label="">
          <el-input-number
            :min="1"
            v-model="options.width"
            placeholder=""
          ></el-input-number>
          
          <el-popover placement="top" trigger="click">
            <div class="sunken">
              <span
                ></span
              >
            </div>
            <i class="fa fa-question-circle-o" slot="reference"> </i>
          </el-popover>
        </el-form-item>
        <el-form-item label="">
          <el-input-number
            :min="1"
            v-model="options.hidth"
            placeholder=""
          ></el-input-number>
          
          <el-popover placement="top" trigger="click">
            <div class="sunken">
              <span
                ></span
              >
            </div>
            <i class="fa fa-question-circle-o" slot="reference"> </i>
          </el-popover>
        </el-form-item>
        <el-form-item label="MIME">
          <el-input
            placeholder=""
            style="width:180px"
            v-model="options.mimeType"
          ></el-input>
          <el-popover placement="top" trigger="click">
            <div class="sunken">
              <span
                >MIMEMIME</span
              >
            </div>
            <i class="fa fa-question-circle-o" slot="reference"> </i>
          </el-popover>
        </el-form-item>
        <el-form-item label="">
          <el-input-number
            :min="0"
            v-model="options.convertSize"
            placeholder="5000000"
          ></el-input-number>
          
          <el-popover placement="top" trigger="click">
            <div class="sunken">
              <span>
                PNGJPEG
              </span>
              <span style="margin-top: 15px; display: block;"></span>
              <el-table
                :data="tableData2"
                :border="true"
                size="small"
                style="margin-top: 15px; width: 600px;"
                stripe
                :header-cell-style="{ background: '#F7F7F7' }"
              >
                <el-table-column
                  prop="convertSize"
                  label=""
                  width="80"
                ></el-table-column>
                <el-table-column
                  prop="inputSize"
                  label=""
                ></el-table-column>
                <el-table-column
                  prop="outputSize"
                  label=""
                ></el-table-column>
                <el-table-column
                  prop="compressionRatio"
                  label=""
                  width="100"
                ></el-table-column>
              </el-table>
            </div>
            <i class="fa fa-question-circle-o" slot="reference"> </i>
          </el-popover>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
const initOptions = {
  quality: 0.8, // 
  strict: true, // 
  checkOrientation: true, // 
  maxWidth: undefined, // 
  maxHeight: undefined, // 
  minWidth: undefined, // 
  minHeight: undefined, // 
  width: undefined, // 
  height: undefined, // 
  mimeType: undefined, // MIME
  convertSize: 0 // 
};

export default {
  model: {
    prop: "value",
    event: "change"
  },

  props: {
    value: {
      type: Object
    }
  },

  computed: {
    options: {
      get() {
        return this.value;
      },
      set(val) {
        this.$emit("change", val);
      }
    }
  },

  watch: {
    options: {
      handler(val) {
        localStorage.setItem("imageCompressOptions", JSON.stringify(val));
      },
      deep: true
    }
  },

  mounted() {
    this.options = JSON.parse(JSON.stringify(initOptions));
  },

  data() {
    return {
      tableData: [
        {
          quality: "0",
          inputSize: "2.12 MB",
          outputSize: "114.61 KB",
          compressionRatio: "94.72%",
          description: "-"
        },
        {
          quality: "0.2",
          inputSize: "2.12 MB",
          outputSize: "349.57 KB",
          compressionRatio: "83.90%",
          description: "-"
        },
        {
          quality: "0.4",
          inputSize: "2.12 MB",
          outputSize: "517.10 KB",
          compressionRatio: "76.18%",
          description: "-"
        },
        {
          quality: "0.6",
          inputSize: "2.12 MB",
          outputSize: "694.99 KB",
          compressionRatio: "67.99%",
          description: ""
        },
        {
          quality: "0.8",
          inputSize: "2.12 MB",
          outputSize: "1.14 MB",
          compressionRatio: "46.41%",
          description: ""
        },
        {
          quality: "1",
          inputSize: "2.12 MB",
          outputSize: "2.12 MB",
          compressionRatio: "0%",
          description: ""
        }
      ],

      tableData2: [
        {
          convertSize: "5 MB",
          inputSize: "1.87 MB (PNG)",
          outputSize: "1.87 MB (PNG)",
          compressionRatio: "0%"
        },
        {
          convertSize: "5 MB",
          inputSize: "5.66 MB (PNG)",
          outputSize: "450.24 KB (JPEG)",
          compressionRatio: "92.23%"
        },
        {
          convertSize: "5 MB",
          inputSize: "9.74 MB (PNG)",
          outputSize: "883.89 KB (JPEG)",
          compressionRatio: "91.14%"
        }
      ]
    };
  },

  methods: {
    onMaxWidthChange(val, oldValue) {
      // 
      if (val < this.options.minWidth) {
        this.options.minWidth = val;
      }
    },

    onMaxHeightChange(val, oldValue) {
      // 
      if (val < this.options.minHeight) {
        this.options.minHeight = val;
      }
    },

    onResetQualityClick() {
      this.options.quality = initOptions.quality;
    },

    onResetOptionsClick() {
      this.options = Object.assign({}, initOptions, {
        quality: this.options.quality
      });
    }
  }
};
</script>
