const util = {}

/**
 * JS
 * @param {String} path JS
 * @param {Function} callback 
 */
function loadJs(path, callback) {
  let secScript = document.createElement("script")
  secScript.setAttribute("src", path)
  document.body.insertBefore(secScript, document.body.lastChild)
  secScript.onload = callback
  if (secScript.readyState) {
    // IE
    secScript.onreadystatechange = function () {
      if (secScript.readyState === 'loaded' || secScript.readyState === 'complete') {
        secScript.onreadystatechange = null
        callback()
      }
    }
  } else {
    //
    secScript.onload = callback
  }
}

/**
 * 
 * @param {String} srcString 
 * @param {String} c 
 * @param {Number} length 
 */
function leftPad(srcString, c, length) {
  if (srcString == null) {
    srcString = ""
  }
  let tLen = srcString.toString().length
  if (tLen >= length) {
    return srcString
  }
  let iMax = length - tLen
  let str = ""
  for (let i = 0; i < iMax; i++) {
    str += c
  }
  str += srcString
  return str
}

/**
 * 26
 */
function getUppercaseLetters() {
  let str = []
  for (let i = 65; i < 91; i++) {
    str.push(String.fromCharCode(i))
  }
  return str
}

/**
 * 26
 */
function getLowercaseLetters() {
  let str = []
  for (let i = 97; i < 123; i++) {
    str.push(String.fromCharCode(i))
  }
  return str
}

/**
 * 
 * @param {String} param0 
 */
function toUpperCamelCase([first, ...rest]) {
  return first.toUpperCase() + rest.join('')
}

/**
 * Blob
 * @param {Blob} blob Blob
 * @param {String} filename blob.name
 */
function downloadFileByBlob(blob, filename) {
  let link = document.createElement('a')
  link.href = URL.createObjectURL(blob)
  link.download = filename || blob.name
  link.click()
}

/**
 * 
 * @param {Number} filesize Bytes
 * @param {String} defaultValue 0
 */
function formatSize(filesize, defaultValue) {
  if (!filesize) {
    return defaultValue || "0KB"
  }
  let unitArr = new Array('B', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB', 'BB', 'NB', 'DB', 'CB')
  let index = 0
  let srcsize = parseFloat(filesize)
  index = Math.floor(Math.log(srcsize) / Math.log(1024))
  let size = srcsize / Math.pow(1024, index)
  size = size.toFixed(2) //
  return size + unitArr[index]
}

export default { loadJs, leftPad, getUppercaseLetters, getLowercaseLetters, toUpperCamelCase, downloadFileByBlob, formatSize }
